import {
  Component
} from '@angular/core';
import { IonicPage, NavController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { Storages } from '../../providers/storages';
import { User } from '../../models/user';
@IonicPage()
@Component({
  selector: 'page-user',
  templateUrl: 'user.html',
  providers: [StatusBar, Storages]
})
export class UserPage {

  user: User;
  constructor(public navCtrl: NavController, private statusBar: StatusBar, private storage: Storages) {
    this.user = new User();
  }
  ngAfterViewInit() {
    //this.statusBar.styleDefault();
    if (this.isUserLogin()) {
      this.user = this.storage.get('User');
    }
  }

  isUserLogin() {
    return !!this.storage.get('User');
  }

  ionViewWillEnter() {
    //this.statusBar.styleDefault();

  }

  enterCollectionPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('CollectionsPage', { user: this.user });
  }

  enterInformationPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('InformationPage', { user: this.user });
  }

  enterSettingPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('SettingPage', { user: this.user });
  }

  enterBiddingPage(section) {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('BiddingPage', { section: section, user: this.user });
  }

  enterCouponPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('CouponPage', { user: this.user });
  }

  enterCreditPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('CreditPage', { user: this.user });
  }
  enterDealsPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('DealPage', { user: this.user });
  }

  enterUnpaidOrdersPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('UnpaidOrdersPage', { user: this.user });
  }
  enterPaidOrdersPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('PaidOrdersPage', { user: this.user });
  }
  enterUnpaidItemsPage() {
    if (!this.isUserLogin()) {
      return this.navCtrl.push('LoginPage');
    }
    this.navCtrl.push('UnpaidItemsPage', { user: this.user });
  }
}



